.swiper-container {
  width: 100%; /* スライダーの幅を調整 */
  height: 100%; /* スライダーの高さを調整 */
}

.swiper-container {
  transform: translate3d(0, 0, 0);
}

/* スライド要素 */
.swiper-slide {
  width: 100%; /* スライドの幅を調整 */
  height: 100%; /* スライドの高さを調整 */
  display: flex; /* スライド内の要素を横並びに配置する場合 */
  align-items: center; /* スライド内の要素を垂直方向に中央揃えする場合 */
  justify-content: right; /* スライド内の要素を水平方向に中央揃えする場合 */
}

.swiper-slide {
  width: 17.5rem;
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  background-position: center;
  background-size: cover;
}

.swiper-wrapper {
  margin: 0rem;
  transform: translate3d(0, 0, 0);
}

.swiper-slide img {
  display: block;
  width: 100%;
}