  width: rem(880);
  height: rem(581);
  right: rem(180);
  
  } 
}
.about__img--3{
  display: none;
  @include mq (md){
    width: rem(194);
    height: rem(181);
    position: absolute;
    right: rem(83);
    bottom: rem(50);
    display: block;
  } 
}